        <span v-html="data.value"></span>
      </template>
      <template v-slot:cell(relatoQuien)="data">
        <span v-html="data.value"></span>
      </template>
      <template v-slot:cell(relatoComo)="data">
        <span v-html="data.value"></span>
      </template>
      <template v-slot:cell(relatoCuando)="data">
        <span v-html="data.value"></span>
      </template>
      <template v-slot:cell(relatoDonde)="data">
